@font-face {
  font-family: "Adinda Melia regular";
  font-style: normal;
  font-weight: normal;
  src: local("Adinda Melia regular"),
    url("./staticFiles/fonts/adinda_melia.woff") format("woff");
}

@font-face {
  font-family: "Brandon Grotesque Light Italic";
  font-style: normal;
  font-weight: normal;
  src: local("Playfair Display regular"),
    url("./staticFiles/fonts/BrandonGrotesque.woff") format("woff");
}

@font-face {
  font-family: "VarelaRound-Regular";
  font-style: normal;
  font-weight: normal;
  src: local("VarelaRound Regular"),
    url("./staticFiles/fonts/VarelaRound-Regular.ttf") format("ttf");
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "VarelaRound Regular", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

@media (min-width: 1200px) and (max-width: 1280px) {
  html {
    font-size: 14px;
  }
}

@media (min-width: 1000px) and (max-width: 1199px) {
  html {
    font-size: 12px;
  }
}

@media (min-width: 800px) and (max-width: 999px) {
  html {
    font-size: 10px;
  }
}

@media (min-width: 600px) and (max-width: 799px) {
  html {
    font-size: 8px;
  }
}
